/* Âü∫Êú¨Ê®£ÂºèË®≠ÂÆö dialog.scss */


.dialog {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    position: relative;
}



/* ‰∏ªÊåâÈàïÊ®£Âºè */
.assemble-button {
    width: 12.5rem;
    height: 6rem;
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 12;
    /* È´òÊñºËÉåÊôØÂúñÁâáÂíåÈÅÆÁΩ©Â±§ */
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    box-shadow: 0px 0px 0px 2px #9fb4f2;
    /* ÊåâÈàïÈÇäÊ°Ü */
    background: linear-gradient(to bottom, #7892c2 5%, #476e9e 100%);
    background-color: #7892c2;
    border-radius: 10px;
    border: 1px solid #4e6096;
    cursor: pointer;
    color: #ffffff;
    padding: 12px 37px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #283966;
}

/* ‰∏ªÊåâÈàïÊá∏ÂÅúÁãÄÊÖãÊ®£Âºè */
.assemble-button:hover {
    background: linear-gradient(to bottom, #476e9e 5%, #7892c2 100%);
    background-color: #476e9e;

}

/* ËÉåÊôØÂúñÁâáÊ®£Âºè */
.custom-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    z-index: 11;
    /* È´òÊñºËÉåÊôØÂúñÁâáÔºåÁ¢∫‰øùÈªûÊìä‰∫ã‰ª∂‰∏çÂÇ≥ÈÅûÂà∞Áõ∏Ê©üÁï´Èù¢ */
}

/* Â∞çË©±Ê°Ü‰∏ªÈ´îÊ®£Âºè */
.custom-modal {
    width: 80vw;
    max-width: 1000px;
    height: 30vh;
    max-height: 80vh;
    background-color: white;
    position: fixed;
    bottom: 5rem;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: flex-start;
    z-index: 12;
    /* È´òÊñºËÉåÊôØÂúñÁâáÂíåÈÅÆÁΩ©Â±§ */
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
}

.custom-modal-title {
    display: none;
}

/* Â∞çË©±Ê°Ü‰∏≠ÁöÑÂúñÁâáÊ®£Âºè */
.custom-modal-image {
    width: 20%;
    height: auto;
    position: absolute;
    left: -10%;
    top: -15%;
    transform: scale(1.5);
    z-index: 13;
    /* È´òÊñºÂ∞çË©±Ê°Ü‰∏ªÈ´î */

}

/* Â∞çË©±Ê°ÜÂÖßÂÆπÊ®£Âºè */
.custom-modal-content {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    margin: auto;
    color: darkgreen;
    width: 95%;
    flex-direction: column;
    padding: 0 5.5rem;
}

/* Â∞çË©±Ê°ÜÊåâÈàïÊ®£Âºè */
.custom-modal-button,
.custom-modal-button-prev {
    position: absolute;
    border: 2px solid #2e3730;
    color: #2e3730;
    cursor: pointer;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 6rem;
    height: 3rem;
    font-weight: bold;
    font-size: 1.3rem;
    bottom: 1rem;
}

.custom-modal-button {
    right: 1rem;
}

.custom-modal-button-prev {
    right: 4rem;
}

/* ÂÄíË®àÊôÇÊ®£Âºè */
.custom-modal-countdown {
    text-align: center;
    font-size: 2rem;
    color: darkgreen;
    position: absolute;
    bottom: 15px;
    left: 40%;
}

/* Â™íÈ´îÊü•Ë©¢ÔºöÈÅ©ÊáâÊâãÊ©üË®≠ÂÇô */
@media screen and (max-width: 768px) {
    .custom-modal {
        width: 90vw;
        max-width: 100%;
        height: 40vh;
        max-height: 90vh;
        bottom: 1rem;
    }

    .custom-modal-image {
        width: 25%;
        height: auto;
        left: -5%;
        top: -50%;
        transform: none;
    }

    .custom-modal-content {
        font-size: 1.5rem;
        padding: 0 5rem;
    }

    .custom-modal-button,
    .custom-modal-button-prev {
        width: 5rem;
        height: 2.5rem;
        font-size: 1rem;
        bottom: 0.5rem;
    }

    .custom-modal-button {
        right: 0.5rem;
    }

    .custom-modal-button-prev {
        right: 4rem;
    }

    .assemble-button {
        width: 10rem;
        height: 5rem;
        font-size: 1.2rem;
    }
}